<template>
  <div id="testApp">
    <div class="uk-section uk-section-secondary uk-light uk-margin-remove">
      <div class="uk-container">
        <h1>
          <a href="https://github.com/ronitrex/PersonalWebsite" target="_blank"
            >ronitrex.github.io API Testing Page</a
          >
        </h1>
        <div class="uk-grid-match uk-child-width-1-2@m" uk-grid>
          <div>
            <h5>
              Welcome to the RESTful API testing page of
              <a
                class="uk-button uk-button-text uk-button-active uk-light"
                href="https://github.com/ronitrex/ronitrex.github.io"
                target="_blank"
                >ronitrex.github.io</a
              >. This page was set up to test the Axios, Express and MongoDB
              based backend of its parent website.
            </h5>
            <ul class="uk-list uk-list-striped">
              <li>Double-click on a card to delete it.</li>
              <li>
                Wait for a few seconds between every event update, like card
                creation or deletion.
              </li>
              <li>
                Github does not allow server-side applications. This page won't
                work when viewed online.
              </li>
              <li>This page is only useful during development.</li>
            </ul>
          </div>
        </div>
        <div
          class="uk-grid-match uk-child-width-1-2@m uk-margin-medium-top"
          uk-grid
        >
          <div>
            <label for="create-card">Create new card:</label>
            <input
              class="uk-margin-small"
              type="text"
              id="create-card"
              v-model="text"
              placeholder="What you type here will be displayed in the card body."
            />
            <button v-on:click="createCard">Create a card!</button>
          </div>
        </div>
      </div>
    </div>

    <div
      class="uk-container uk-container-expand uk-margin uk-margin-small uk-padding uk-padding-large"
    >
      <div
        class="uk-child-width-1-2@m uk-grid uk-grid-small uk-grid-match"
        uk-grid
        uk-scrollspy="cls: uk-animation-fade; target: .uk-card; repeat: true"
      >
        <div
          v-for="(card, index) in cards"
          v-bind:card="card"
          v-bind:index="index"
          v-bind:key="card._id"
          v-on:dblclick="deleteCard(card._id)"
        >
          <ProjectCards
            :description="card.text"
            :items="card.items"
            :label="`working`"
            :stack="`Created At : ${card.createdAt.getDate()}/${card.createdAt.getMonth()}/${card.createdAt.getFullYear()}`"
            :name="card.name"
            :link="card.link"
          ></ProjectCards>
        </div>
      </div>
    </div>
    <p class="error" v-if="error">{{}}</p>
  </div>
</template>

<script>
import UIkit from "uikit";
import Icons from "uikit/dist/js/uikit-icons";
import cardService from "./cardService";

UIkit.use(Icons);

import ProjectCards from "../components/ProjectCards.vue";
// import Sidebar from "./components/SideBar.vue"
// import TopBar from "./components/TopBar.vue"

export default {
  name: "testApp",
  components: {
    ProjectCards,
  },
  data() {
    return {
      cards: [],
      error: "",
      text: "",
    };
  },
  async created() {
    try {
      this.cards = await cardService.getCards();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async createCard() {
      await cardService.insertCard(this.text);
      this.cards = await cardService.getCards();
    },
    async deleteCard(id) {
      await cardService.deleteCard(id);
      this.cards = await cardService.getCards();
    },
  },
};
</script>

<style lang="less">
* {
  @import "../../node_modules/uikit/src/less/uikit.theme.less";
  margin: 0;
  font-family: "overpass", sans-serif;
  font-weight: 600;
  font-style: normal;
}
</style>
